$squareSide: 100px;
$pixel: 1px;
$calendarWidth: 7 * $squareSide + 6 * $pixel;
$retreat: $squareSide + $pixel;
$fullMonth: 31;
$shortMonth: 28;
$startMonth: 1;

$week:
  "mon" 0,
  "tue" $retreat,
  "wen" 2 * $retreat,
  "thu" 3 * $retreat,
  "fri" 4 * $retreat,
  "sat" 5 * $retreat,
  "sun" 6 * $retreat;
